
.card-container{
  display:flex;
  flex-direction: column;
  padding:10px;
  justify-content: flex-start;
  gap:10px;
  align-items: center; 
  background-color:white;
  font-size: 14px;
  border-radius:5px;
}

.card-container.left{
  border-bottom: 1px solid #EAEAEA;
  border-right:1px solid #EAEAEA;
  height:120px;
}

.card-container.right{
  border-bottom: 1px solid #EAEAEA;
  border-left:1px solid #EAEAEA;
  height:120px;
}

.box-border{
  border:1px solid #EAEAEA;
  min-width:190px;
  height:70px;
}

.horizontal_cards{
  display:flex;
  gap:20px;
  overflow:auto;
  padding:20px;
}

.vertical_cards{
  display:grid;
  grid-template-columns: 50% 50%;
  padding:20px;
}